import React from "react";
import location from "../assets/images/location.svg";

export const Dispatch = () => {
	return (
		<div className="dispatch-container">
			<div className="container-center-start">
				<img className="location-image" src={location} alt="" />
				<span>Consulta disponibilidad de despacho</span>
			</div>
			<div className="container-flex-center">
				<p className="down">Comuna/tienda más cercana</p>
				<select
					name="select"
					className="color-gray select-dispatch"
					defaultValue="algo"
				>
					<option value="value1" selected>
						Selecciona tu Comuna/tienda más cercana
					</option>
					<option value="value2">Arica</option>
					<option value="value3">Camarones</option>
				</select>
			</div>
		</div>
	);
};
